// General

.header {
  padding-top: 20px;
  padding-bottom: 60px;

  @include breakpoint(tablet) {
    padding-bottom: 50px;
  }

  @include breakpoint(desktop) {
    padding-bottom: 50px;
  }
}

.nav {
  margin-left: auto;
  @include breakpoint(desktop) {
    margin-left: 43px;
  }
}

.nav-flex {
  display: flex;
  align-items: baseline;
  @include breakpoint(tablet) {
    display: flex;
    margin-left: auto;
    margin-right: auto;
  }
}

.nav-list {
  display: none;

  @include breakpoint(desktop) {
    display: flex;
  }
}

.nav-list__item {
  cursor: pointer;
}

.nav-list__item:not(:last-child) {
  @include breakpoint(desktop) {
    margin-right: 60px;
  }
}

.nav-list__link {
  &:hover,
  &:focus {
    color: var(--accent-color);
  }
  @include setTransition(color);
}

.responsive {
  padding-top: 20px;
  display: flex;
  align-items: end;
  margin-left: auto;
  margin-right: auto;
}

.icon {
  margin-left: 12px;
}

.menu-open {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  background-color: transparent;
  border: none;
  cursor: pointer;
  @include breakpoint(tablet) {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  @include breakpoint(desktop) {
    @include visibilityHidden();
  }
}

.menu-close {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: auto;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

// Desktop searchbox

.input {
  position: relative;
  color: #5f6775;
  width: 280px;
  height: 40px;
  padding: 8px;
  border-radius: 0px 4 4 0px;
  border: 1px solid var(--accent-color);
  border-left: none;
  background-color: transparent;

  @include breakpoint(false, tablet) {
    @include visibilityHidden();
  }
}

.form__input-icon {
  padding: 9px;
  fill: var(--accent-color);
}

.form__input-wrapper {
  display: flex;
}

.form__input__button {
  padding: 9px;
  background: transparent;
  border: 1px solid var(--accent-color);
  border-radius: 4px 0 0 4px;
}

.form__input__icon {
  fill: var(--accent-color);
}

// Mobile/tablet searchbox

.mobile-input__wrapper {
  display: flex;
  margin-top: 37px;
  justify-content: center;

  @include breakpoint(tablet) {
    @include visibilityHidden();
  }
}

.mobile__input {
  color: #5f6775;
  position: relative;
  height: 40px;
  padding: 8px;
  border-radius: 0px 4 4 0px;
  border: 1px solid var(--accent-color);
  border-left: none;
  background-color: transparent;
}

.mode__container {
  display: flex;
  align-items: center;
  margin-left: 172px;
  @include breakpoint(false, desktop) {
    @include visibilityHidden();
  }
}

.usability {
  display: flex;
  margin-left: auto;

  @include breakpoint(false, tablet) {
    margin-left: 190px;
  }
  @include breakpoint(false, tablet) {
    @include visibilityHidden();
  }
}

// Dropdown desktop

.drop {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  background-color: transparent;
  border: 1px solid var(--accent-color);
  border-radius: 4px;
  width: 210px;

  background-color: var(--background-color);

  @include setTransition(background-color);
}

.drop-wrapper {
  margin-top: 16px;
}

.nav-list li:hover ul {
  opacity: 1;
  visibility: visible;
}

.drop li:hover {
  color: var(--white-color);

  background-color: var(--accent-color);
  @include setTransition(background-color, opacity);
}

.drop-list__item {
  padding: 12px;

  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;
}

.drop-list__item:hover {
  &:not(:last-child) {
    border-bottom: 1px solid white;
  }
  &:not(:first-child) {
    border-top: 1px solid white;
  }
}

.mobile__svg__button {
  background: transparent;
}

// Dropdown mobile

.dropdown {
  position: relative;
}

.dropdown-content {
  display: none;
  margin-top: 16px;
  width: 100%;
  background-color: transparent;
  border: 1px solid var(--accent-color);
  border-radius: 4px;
  width: 210px;
}

.dropdown:hover .dropdown-content {
  display: block;
  @include setTransition(background-color);
}

.dropdown-content a {
  display: block;
  padding: 12px;
  text-decoration: none;
}

.mobile-dropdown__item {
  border-bottom: 1px solid transparent;
  border-top: 1px solid transparent;

  &:hover {
    &:not(:last-child) {
      border-bottom: 1px solid white;
    }
    &:not(:first-child) {
      border-top: 1px solid white;
    }
  }
}

.dropdown-content a:hover {
  background-color: var(--accent-color);
  color: var(--white-color);
}

.dropdown-button__wrapper {
  display: flex;
  align-items: center;
}

.dropdown__button {
  background: transparent;
  margin-right: 8px;
}

.drop-dark {
  border: 1px solid var(--white-color);
  overflow: hidden;
}

// Mobile menu

.mobile__logo {
  @include breakpoint(false, tablet) {
    @include visibilityHidden();
  }
}

.mob-menu {
  position: fixed;
  top: -0.2px;
  z-index: 2;
  width: 100%;
  height: 100%;
  background-color: var(--background-color);
}

.mob-menu.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.mobile-mode__container {
  display: flex;
  align-items: center;
  font-size: 14px;
  @include breakpoint(tablet) {
    margin-left: 150px;
    font-size: 18px;
  }
}

.mob-menu-list {
  padding-top: 40px;

  @include breakpoint(tablet) {
    padding-left: 306px;
  }
}

.mob-menu-list__item:not(:last-child) {
  margin-bottom: 32px;
}

.mob-menu-list__link {
  display: flex;
  align-items: center;
}

// Toggle switch desktop

.toggle {
  display: flex;
  position: relative;
  height: 20px;
  width: 40px;
  border-radius: 100px;
  background-color: transparent;
  margin-left: 12px;
  margin-right: 12px;
  border: 1px solid var(--accent-color);
  cursor: pointer;

  @include breakpoint(false, desktop) {
    @include visibilityHidden();
  }
}

.toggle::before {
  content: '';
  position: absolute;
  height: 14px;
  width: 14px;
  background: var(--accent-color);
  border-radius: 50%;
  top: 50%;
  right: 22px;
  transform: translateY(-50%);
  @include setTransition(right);
}

.toggle.active::before {
  background-color: var(--white-color);
  right: calc(100% - 14px - 22px);
}

.toggle.active {
  border: 1px solid white;
  background-color: var(--accent-color);
}

.accent {
  color: var(--accent-color);
}

// Toggle switch mobile

.mobile__toggle {
  display: flex;
  position: relative;
  height: 20px;
  width: 40px;
  border-radius: 100px;
  background-color: transparent;
  margin-left: 12px;
  margin-right: 12px;
  border: 1px solid var(--accent-color);
  cursor: pointer;
}

.mobile__toggle::before {
  content: '';
  position: absolute;
  height: 14px;
  width: 14px;
  background: var(--accent-color);
  border-radius: 50%;
  top: 50%;
  right: 22px;
  transform: translateY(-50%);
  @include setTransition(right);
}

.mobile__toggle.active::before {
  background-color: var(--white-color);
  right: calc(100% - 14px - 22px);
}

.mobile__toggle.active {
  border: 1px solid white;
  background-color: var(--accent-color);
}

.act {
  color: var(--accent-color);
}
