@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Poppins:wght@300;400;500;600;700;900&family=Roboto:wght@400;700;900&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --black-color: #000;
  --white-color: #fff;
  --accent-color: #fd5103;
  --primary-button-color: #fe7031;
  --hover-button-color: #f5f5f5;
  --gray-color: #5f6775;
  --pagination-number-color: #1e1e1e;
  --primary-text-color: #202025;
  --background-color: #fcfcfc;
  --modal-text-color: #5f6775;
  --modal-title-color: #000917;
  --animation-duration: 250ms;
}

h1, h2, h3, p, ul {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
  display: inline-block;
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input {
  outline: none;
}

button {
  cursor: pointer;
  color: inherit;
  background: none;
  border: none;
  padding: 0;
}

svg {
  fill: currentColor;
  display: block;
}

body.dark {
  --background-color: #202025;
  --primary-text-color: #fcfcfc;
  --pagination-number-color: #fcfcfc;
  --modal-text-color: #bdbdbd;
  --modal-title-color: #fcfcfc;
}

body.scroll-lock {
  overflow: hidden;
}

body {
  color: var(--primary-text-color);
  background-color: var(--background-color);
  font-family: Poppins, sans-serif;
  font-size: 18px;
  line-height: 1.33333;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.container {
  --padding: 20px;
  max-width: 480px;
  padding-left: var(--padding);
  padding-right: var(--padding);
}

@media screen and (min-width: 480px) {
  .container {
    margin: 0 auto;
  }
}

@media screen and (min-width: 768px) {
  .container {
    --padding: 32px;
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    --padding: 15px;
    max-width: 1280px;
  }
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.section__title {
  text-align: center;
  color: var(--primary-text-color);
  margin-bottom: 60px;
  font-size: 22px;
  font-weight: 600;
  line-height: 1.90909;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.section__title--fail {
  width: 280px;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 768px) {
  .section__title {
    font-size: 32px;
    font-weight: 700;
    line-height: 1.3125;
  }

  .section__title--fail {
    width: 493px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (min-width: 1280px) {
  .section__title {
    font-size: 48px;
    line-height: 1.5;
  }
}

.section__title--fail {
  width: 493px;
  margin-left: auto;
  margin-right: auto;
}

.section__not-found {
  text-align: center;
  color: var(--primary-text-color);
  font-size: 20px;
  line-height: 3;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .section__not-found {
    font-size: 24px;
    line-height: 2.5;
  }
}

@media screen and (min-width: 1280px) {
  .section__not-found {
    font-size: 32px;
    line-height: 2.25;
  }
}

.section .list {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .section .list {
    flex-flow: wrap;
    gap: 32px;
  }
}

.section .not-found--hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.button {
  --heart-color: var(--background-color);
  letter-spacing: .02em;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  padding: 7px 16px;
  font-family: Roboto;
  font-size: 14px;
  line-height: 1.71429;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

@media screen and (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 1.2;
  }
}

.button--primary {
  min-width: 110px;
  color: var(--white-color);
  background-color: var(--primary-button-color);
  border-radius: 4px;
}

.button--primary:hover, .button--primary:focus {
  color: var(--accent-color);
  background-color: var(--hover-button-color);
}

@media screen and (min-width: 768px) {
  .button--primary {
    padding: 12px 18px;
  }
}

@media screen and (min-width: 1280px) {
  .button--primary {
    padding: 12px 30px;
  }
}

.button--secondary {
  min-width: 110px;
  color: var(--accent-color);
  border: 1px solid var(--accent-color);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 4px;
}

.button--secondary:hover, .button--secondary:focus {
  box-shadow: 0px 0px 0px 1px var(--accent-color);
  background-color: var(--white-color);
  --heart-color: var(--white-color);
}

@media screen and (min-width: 768px) {
  .button--secondary {
    padding: 12px 24px 12px 13px;
  }
}

@media screen and (min-width: 1280px) {
  .button--secondary {
    border-width: 2px;
    padding: 12px 26px 12px 24px;
  }
}

.button[data-favorite="true"] {
  --heart-color: var(--accent-color);
}

.button__icon {
  color: var(--heart-color);
  margin-left: 5px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.cocktail-card {
  min-width: 280px;
  max-width: 100%;
  border: 1px solid var(--accent-color);
  border-radius: 4px;
  padding-bottom: 24px;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .cocktail-card {
    width: calc(50% - 16px);
    padding-bottom: 20px;
  }
}

@media screen and (min-width: 1280px) {
  .cocktail-card {
    width: calc(33.3333% - 21.3333px);
    padding-bottom: 28px;
  }
}

.cocktail-card__image {
  margin-bottom: 48px;
}

@media screen and (min-width: 768px) {
  .cocktail-card__image {
    margin-bottom: 20px;
  }
}

.cocktail-card__title {
  text-align: center;
  margin-bottom: 16px;
  padding: 0 28px;
  font-family: Roboto;
  font-size: 26px;
  font-style: normal;
  font-weight: 900;
  line-height: 1.61538;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .cocktail-card__title {
    max-height: 38px;
    margin-bottom: 25px;
    font-size: 28px;
    line-height: 1.17857;
  }
}

@media screen and (min-width: 1280px) {
  .cocktail-card__title {
    margin-bottom: 27px;
    font-size: 32px;
    line-height: 1.1875;
  }
}

.cocktail-card__button {
  justify-content: center;
  column-gap: 8px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .cocktail-card__button {
    column-gap: 4px;
  }
}

@media screen and (min-width: 1280px) {
  .cocktail-card__button {
    column-gap: 8px;
  }
}

.cocktail-card__buttons-wrapper {
  justify-content: center;
  column-gap: 17px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .cocktail-card__buttons-wrapper {
    column-gap: 12px;
  }
}

@media screen and (min-width: 1280px) {
  .cocktail-card__buttons-wrapper {
    column-gap: 16px;
  }
}

.cocktail-card__button-icon {
  width: 19px;
  height: 17px;
}

@media screen and (min-width: 768px) {
  .cocktail-card__button-icon {
    width: 21px;
    height: 19px;
  }
}

.ingredient-card {
  border: 1px solid var(--accent-color);
  border-radius: 4px;
  flex-direction: column;
  padding: 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .ingredient-card {
    flex-basis: calc(50% - 16px);
    padding: 33px 20px 20px;
  }
}

@media screen and (min-width: 1280px) {
  .ingredient-card {
    flex-basis: calc(33.3333% - 21.3333px);
    padding: 27px;
  }
}

.ingredient-card__title {
  margin-bottom: 20px;
  font-family: Roboto;
  font-size: 26px;
  font-style: normal;
  font-weight: 900;
  line-height: 1.61538;
}

@media screen and (min-width: 768px) {
  .ingredient-card__title {
    max-height: 38px;
    margin-bottom: 28px;
    font-size: 28px;
    line-height: 1.17857;
  }
}

@media screen and (min-width: 1280px) {
  .ingredient-card__title {
    font-size: 32px;
    line-height: 1.1875;
  }
}

.ingredient-card__subtitle {
  text-transform: uppercase;
  color: var(--modal-text-color);
  margin-bottom: 20px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.5;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .ingredient-card__subtitle {
    color: var(--modal-text-color);
    margin-bottom: 28px;
    font-size: 16px;
    line-height: 1.5;
  }
}

.ingredient-card__buttons-wrapper {
  justify-content: center;
  column-gap: 22px;
  margin-top: auto;
  display: flex;
}

@media screen and (min-width: 768px) {
  .ingredient-card__buttons-wrapper {
    column-gap: 12px;
  }
}

@media screen and (min-width: 1280px) {
  .ingredient-card__buttons-wrapper {
    column-gap: 16px;
  }
}

.details {
  width: 100%;
  height: 100%;
}

.details__top-wrap {
  flex-direction: column;
  display: flex;
}

@media screen and (min-width: 768px) {
  .details__top-wrap {
    flex-direction: row;
    margin-bottom: 28px;
  }
}

@media screen and (min-width: 768px) {
  .details__img {
    width: 288px;
    height: 320px;
    object-fit: cover;
  }
}

@media screen and (min-width: 768px) {
  .details__inner-wrap {
    padding-left: 32px;
  }
}

.details__title {
  color: var(--primary-text-color);
  margin-bottom: 20px;
  font-family: Roboto;
  font-size: 32px;
  font-weight: 900;
  line-height: 1.1875;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .details__title {
    margin-bottom: 28px;
  }
}

.details__ingredient-title {
  color: var(--primary-text-color);
  margin-bottom: 8px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.5;
}

.details__ingredient-remark {
  color: var(--primary-text-color);
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 300;
  line-height: 1.5;
}

@media screen and (max-width: 767px) {
  .details__ingredient-list {
    margin-bottom: 32px;
  }
}

@media screen and (min-width: 768px) {
  .details__ingredient-list {
    flex-direction: column;
    display: flex;
  }
}

.details__ingredient-link {
  cursor: pointer;
  color: var(--modal-text-color);
  font-size: 16px;
  line-height: 1.625;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.details__ingredient-link:hover, .details__ingredient-link:focus, .details__ingredient-link:hover:before, .details__ingredient-link:focus:before {
  color: var(--accent-color);
}

.details__ingredient-link:before {
  content: "✶";
  color: var(--primary-text-color);
  margin-right: 5px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.details__instruction-text {
  display: none;
}

@media screen and (min-width: 768px) {
  .details__instruction-text {
    width: 615px;
    max-height: 112px;
    color: var(--modal-text-color);
    margin-bottom: 40px;
    margin-right: 90px;
    padding-right: 8px;
    font-size: 16px;
    line-height: 1.75;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
    overflow-y: auto;
  }
}

.details__img-wrap {
  display: none;
}

@media screen and (min-width: 768px) {
  .details__img-wrap {
    width: 288px;
    height: 320px;
    margin-top: -40px;
    margin-left: -28px;
    display: block;
  }
}

.details__instruction-title {
  display: none;
}

@media screen and (min-width: 768px) {
  .details__instruction-title {
    text-transform: uppercase;
    color: var(--primary-text-color);
    margin: 0 0 24px;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.625;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }
}

.details .details__add-btn {
  display: block;
}

@media screen and (min-width: 768px) {
  .details .details__add-btn {
    margin-left: auto;
  }
}

.details__instruction-text-mobile {
  color: var(--gray-color);
  margin-bottom: 32px;
  font-size: 16px;
  line-height: 1.75;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .details__instruction-text-mobile {
    display: none;
  }
}

.details__instruction-title-mobile {
  text-transform: uppercase;
  color: var(--primary-text-color);
  margin: 0 0 20px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.625;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .details__instruction-title-mobile {
    display: none;
  }
}

.details__img-wrap-mobile {
  margin-bottom: 22px;
}

@media screen and (min-width: 768px) {
  .details__img-wrap-mobile {
    display: none;
  }
}

.ingredient {
  width: 100%;
  height: 100%;
}

.ingredient__title {
  color: var(--primary-text-color);
  margin-bottom: 20px;
  font-family: Roboto;
  font-size: 32px;
  font-weight: 900;
  line-height: 1.1875;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .ingredient__title {
    margin-bottom: 28px;
  }
}

.ingredient__type {
  text-transform: uppercase;
  color: var(--primary-text-color);
  margin-bottom: 20px;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.5;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .ingredient__type {
    margin-bottom: 20px;
  }
}

.ingredient__line {
  display: none;
}

@media screen and (min-width: 768px) {
  .ingredient__line {
    width: 100%;
    height: 1px;
    background-color: var(--accent-color);
    margin-bottom: 28px;
    display: block;
  }
}

.ingredient__kind {
  color: var(--modal-text-color);
  margin-bottom: 32px;
  font-size: 16px;
  line-height: 1.625;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.ingredient__kind:before {
  content: "✶";
  color: var(--modal-title-color);
  margin-right: 5px;
  display: inline-block;
}

@media screen and (min-width: 768px) {
  .ingredient__kind {
    margin-bottom: 40px;
  }
}

.ingredient__description {
  color: var(--modal-text-color);
  margin-bottom: 28px;
  font-size: 16px;
  line-height: 1.75;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  overflow-y: auto;
}

.ingredient__description > span {
  color: var(--primary-text-color);
  font-weight: 600;
}

@media screen and (min-width: 768px) {
  .ingredient__description {
    width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .ingredient__description {
    min-width: 654px;
    max-height: 340px;
    padding-right: 10px;
  }
}

.ingredient__add-btn {
  display: block;
}

@media screen and (min-width: 768px) {
  .ingredient__add-btn {
    margin-left: auto;
  }
}

.pagination__container {
  justify-content: center;
  margin-top: 32px;
  display: flex;
}

@media screen and (max-width: 767px) {
  .pagination__container {
    margin-top: 40px;
  }
}

.pagination__arrow {
  color: var(--primary-text-color);
  background-color: var(--background-color);
}

.pagination__arrow--prev {
  transform: rotate(180deg);
}

.pagination__arrow.disabled {
  cursor: not-allowed;
  color: var(--gray-color);
}

.pagination__arrow:not(.disabled):hover {
  color: var(--accent-color);
}

.pagination__number {
  background-color: var(--background-color);
  color: var(--pagination-number-color);
  width: 40px;
  height: 40px;
  border: 1px solid var(--primary-button-color);
  border-radius: 50%;
  margin-right: 12px;
  font-family: Montserrat, sans-serif;
  font-size: 16px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (max-width: 767px) {
  .pagination__number {
    width: 30px;
    height: 30px;
    margin-right: 8px;
    font-size: 12px;
  }

  .pagination__number:first-child {
    margin-left: 8px;
  }
}

@media screen and (min-width: 768px) {
  .pagination__number:first-child {
    margin-left: 12px;
  }
}

.pagination__number.active {
  color: var(--background-color);
  background-color: var(--primary-button-color);
}

.pagination__number:hover {
  color: var(--background-color);
  background: var(--accent-color);
}

.pagination__dots {
  color: var(--primary-button-color);
  margin-right: 12px;
  font-size: 12px;
}

@media screen and (max-width: 767px) {
  .pagination__dots {
    margin-right: 8px;
  }
}

.hidden {
  display: none;
}

.pagination__cards {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .pagination__cards {
    flex-flow: wrap;
    gap: 32px;
  }
}

.not-found-img {
  justify-content: center;
  display: flex;
}

@media screen and (max-width: 767px) {
  .not-found-img {
    width: 280px;
    height: 308px;
    margin: -35px auto 40px;
  }
}

.modal {
  opacity: 1;
  background-color: rgba(0, 0, 0, .4);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
}

.modal--none {
  display: none;
}

.modal--hidden {
  pointer-events: none;
  opacity: 0;
}

.modal__content {
  --padding-x: 20px;
  --padding-y: 40px;
  padding: var(--padding-y) var(--padding-x);
